import React, {
  useState,
} from "react";
import assert from "assert";

import ButtonGroup from "react-bootstrap/ButtonGroup";
import ToggleButton from "react-bootstrap/ToggleButton";

export default function ToggleButtons(
  {
    selects,
    groupName,
    onChange,
  }: {
    selects: Array<{ id: number, value: number, name: string }>,
    groupName: string,
    onChange: (_: number) => void,
  },
): React.ReactElement {
  const [selectedId, setSelectedId] = useState(0);
  return (
    <ButtonGroup className="flex-wrap">
      {selects.map(({ id, name }) => (
        <ToggleButton
          key={id}
          id={`${groupName}-${id}`}
          name={`${groupName}s-radio`}
          type="radio"
          variant="outline-primary"
          value={id}
          checked={selectedId === id}
          onChange={(e) => {
            const changeId = Number(e.currentTarget.value);
            setSelectedId(changeId);
            const select = selects.find((s) => s.id === changeId);
            assert(select);
            onChange(select.value);
          }}
        >
          {name}
        </ToggleButton>
      ))}
    </ButtonGroup>
  );
}
