import React, {
  useState,
  useEffect,
} from "react";

import InputGroup from "react-bootstrap/InputGroup";
import Form from "react-bootstrap/Form";

import { updateAmountByStr, updateIntegerByStr } from "../../utils/utils";

export default function RangeInput(
  {
    text,
    placeholder,
    defaultValue,
    min,
    max,
    onChange,
    decimal,
  }: {
    text: string,
    placeholder: string,
    defaultValue: string,
    min: bigint,
    max: bigint | null,
    onChange: (_: bigint | null) => void,
    decimal?: number,
  },
): React.ReactElement {
  const [raw, setRaw] = useState(defaultValue);
  const [isInvalid, setIsInvalid] = useState(false);

  function update(value: string) {
    if (!decimal || decimal === 0) {
      updateIntegerByStr(value, setRaw, (v) => {
        if (v === null || v < min || (max !== null && max <= v)) {
          onChange(null);
          setIsInvalid(true);
        } else {
          onChange(v);
          setIsInvalid(false);
        }
      });
    } else {
      updateAmountByStr(value, setRaw, (v) => {
        if (
          v === null
          || v < min * BigInt(10 ** 18)
          || (max !== null && max * BigInt(10 ** 18) <= v)
        ) {
          onChange(null);
          setIsInvalid(true);
        } else {
          onChange((v * BigInt(10 ** decimal)) / BigInt(10 ** 18));
          setIsInvalid(false);
        }
      });
    }
  }

  // eslint-disable-next-line
  useEffect(() => update(defaultValue), [defaultValue]);

  return (
    <InputGroup hasValidation>
      <InputGroup.Text>{text}</InputGroup.Text>
      <Form.Control
        value={raw}
        placeholder={placeholder}
        onChange={
          (e) => {
            const { value } = e.currentTarget;
            update(value);
          }
        }
        isInvalid={isInvalid}
      />
    </InputGroup>
  );
}

RangeInput.defaultProps = {
  decimal: 0,
};
