import React from "react";

import { Switch, Route, Redirect } from "react-router-dom";

import Home from "./views/Home/Home";
import NFT from "./views/NFT/NFT";

export default function Routes(): React.ReactElement {
  return (
    <Switch>
      <Route path="/nft">
        <NFT />
      </Route>
      <Route path="/home">
        <Home />
      </Route>
      <Route path="/">
        <Redirect to="/home" />
      </Route>
    </Switch>
  );
}
