import { createContext, useEffect } from "react";

import { BaseProvider } from "@metamask/providers";

export const EthereumContext = createContext<BaseProvider | null>(null);

export const AccountContext = createContext<string | null>(null);

export function useMountState(callback: () => void): void {
  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(callback, []);
}

export function EMPTY_FUNCTION(): void { /* ignore promise handling */ }

export function validatePositiveInteger(raw: string): bigint | null {
  try {
    const value = BigInt(raw);
    if (String(value) === raw && value > 0) {
      return value;
    }
  } catch (_) { /* do nothing */ }
  return null;
}

const FIXED_DECIMAL_UNIT = BigInt(10 ** 18);
export function validatePositiveFixedDecimal(raw: string): bigint | null {
  try {
    const value = raw.split(".");
    if (value.length > 2 || value.length === 0) {
      return null;
    }
    const left = validatePositiveInteger(value[0]);
    if (value.length === 1) {
      if (!left) {
        return null;
      }
      return left * FIXED_DECIMAL_UNIT;
    }
    if (value[0] !== "0" && value[0] !== "" && !left) {
      return null;
    }
    const full = validatePositiveInteger(raw.replace(".", "").replace(/^0*/g, ""));
    if (!full || value[1].length > 18) {
      return null;
    }
    return full * BigInt(10 ** (18 - value[1].length));
  } catch (_) { /* do nothing */ }
  return null;
}

export function fixedDecimalToStr(value: bigint, decimal: number): string {
  const left = value / BigInt(10 ** 18);
  const right = value - left * BigInt(10 ** 18);
  if (right === BigInt(0) || decimal === 0) {
    return String(left);
  }
  let rightRaw = String(right).padStart(18, "0");
  if (rightRaw.length > decimal) {
    rightRaw = rightRaw.slice(0, decimal);
  }
  return `${left}.${rightRaw}`.replace(/0*$/g, "");
}

export function updateAmountByStr(
  raw: string,
  setter: (_: string) => void,
  valSetter?: (_: bigint | null) => void,
): void {
  const value = validatePositiveFixedDecimal(raw);
  if (raw === "" || raw === "0" || raw.match(/^0\.0*$/g) || value !== null) {
    setter(raw);
    if (valSetter) {
      valSetter(value);
    }
  }
}

export function updateIntegerByStr(
  raw: string,
  setter: (_: string) => void,
  valSetter?: (_: bigint | null) => void,
): void {
  const value = validatePositiveInteger(raw);
  if (raw === "" || value !== null) {
    setter(raw);
    if (valSetter) {
      valSetter(value);
    }
  }
}
