import "./App.scss";

import React, { useState, useEffect, useCallback } from "react";
import { HashRouter, NavLink } from "react-router-dom";

import detectEthereumProvider from "@metamask/detect-provider";
import { BaseProvider } from "@metamask/providers";

import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Row from "react-bootstrap/Row";

import { EthereumContext, AccountContext, EMPTY_FUNCTION } from "./utils/utils";

import Routes from "./Routes";

const ERROR_NETWORK_NOT_ADDED = 4902;
const CHAIN_ID = "0x42";

async function switchToChain(ethereum: BaseProvider) {
  try {
    await ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: CHAIN_ID }],
    });
  } catch (switchError) {
    const { code }: { code: number } = switchError as { code: number };

    if (code !== ERROR_NETWORK_NOT_ADDED) {
      return code;
    }
  }

  return null;
}

async function prepareChain(ethereum: BaseProvider) {
  const switched = await switchToChain(ethereum);
  if (switched && switched !== ERROR_NETWORK_NOT_ADDED) {
    return false;
  }

  try {
    await ethereum.request({
      method: "wallet_addEthereumChain",
      params: [{
        chainId: CHAIN_ID,
        chainName: "OKExChain Mainnet",
        nativeCurrency: { name: "OKT", symbol: "OKT", decimals: 18 },
        rpcUrls: ["https://exchainrpc.okex.org"],
        blockExplorerUrls: ["https://www.oklink.com/okexchain"],
      }],
    });
  } catch (addError) {
    return false;
  }

  return !(await switchToChain(ethereum));
}

function App(): React.ReactElement {
  const [ethereum, setEthereum] = useState<BaseProvider | null>(null);
  const [account, setAccount] = useState<string | null>(null);
  const [isOnChain, setIsOnChain] = useState(false);

  detectEthereumProvider()
    .then(
      (provider) => { (setEthereum as (_: unknown) => void)(provider); },
      EMPTY_FUNCTION,
    );

  const updateSelectedAccount = useCallback(
    () => {
      if (!ethereum) {
        return;
      }

      setAccount(ethereum.selectedAddress);
    },
    [ethereum],
  );

  function connect() {
    if (!ethereum) {
      return;
    }
    ethereum.request({ method: "eth_requestAccounts" })
      .then(updateSelectedAccount, EMPTY_FUNCTION);
  }

  useEffect(
    () => {
      if (!ethereum) {
        return EMPTY_FUNCTION;
      }

      ethereum.request({ method: "eth_accounts" })
        .then(updateSelectedAccount, EMPTY_FUNCTION);

      ethereum.on("accountsChanged", updateSelectedAccount);
      return () => {
        ethereum.removeListener("accountsChanged", updateSelectedAccount);
      };
    },
    [ethereum, updateSelectedAccount],
  );

  useEffect(
    () => {
      if (!ethereum) {
        return;
      }

      prepareChain(ethereum).then(setIsOnChain, EMPTY_FUNCTION);
    },
    [ethereum],
  );

  if (!ethereum) {
    return <div>You must installed MetaMask to use Celestial dashboard!</div>;
  }

  if (!isOnChain) {
    return (<div>You are not on chain!</div>);
  }

  if (!account) {
    return (
      <Container>
        <Row className="justify-content-center">
          <Col xs="auto">You must connect to MetaMask to use Celestial dashboard!</Col>
        </Row>
        <Row className="justify-content-center">
          <Col xs="auto">
            <Button onClick={() => connect()}>Connect</Button>
          </Col>
        </Row>
      </Container>
    );
  }

  return (
    <EthereumContext.Provider value={ethereum}>
      <AccountContext.Provider value={account}>
        <HashRouter>
          <Navbar>
            <Container>
              <Nav>
                <NavLink to="/home" className="me-3 text-decoration-none">
                  Airdrop
                </NavLink>
                <NavLink to="/nft" className="text-decoration-none">
                  NFT airdrop
                </NavLink>
              </Nav>
              <Navbar.Brand>{account}</Navbar.Brand>
            </Container>
          </Navbar>
          <Routes />
        </HashRouter>
      </AccountContext.Provider>
    </EthereumContext.Provider>
  );
}

export default App;
