import "./Home.scss";

import React, {
  useState,
  useContext,
  useEffect,
} from "react";
import Web3 from "web3";
import { AbiItem } from "web3-utils";
import assert from "assert";

import Button from "react-bootstrap/Button";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import InputGroup from "react-bootstrap/InputGroup";
import Row from "react-bootstrap/Row";
import Spinner from "react-bootstrap/Spinner";
import Table from "react-bootstrap/Table";
import ToggleButton from "react-bootstrap/ToggleButton";

import { BaseProvider } from "@metamask/providers";

import {
  EthereumContext,
  AccountContext,
  EMPTY_FUNCTION,
  updateAmountByStr,
  updateIntegerByStr,
  validatePositiveFixedDecimal,
  fixedDecimalToStr,
} from "../../utils/utils";

class To {
  address: string;

  amount: bigint | null;

  constructor(address: string, amount: bigint | null) {
    this.address = address;
    this.amount = amount;
  }
}

function parseAddresses(raw: string): To[] | null {
  const toList: To[] = [];
  let previousAddress: To | null = null;

  const isValid = raw
    .replaceAll(",", " ")
    .replaceAll("\n", " ")
    .replaceAll('"', "")
    .replaceAll("'", "")
    .replaceAll("`", "")
    .replaceAll(/  */g, " ")
    .trim()
    .split(" ")
    .every((value) => {
      if (value.startsWith("0x")) {
        previousAddress = new To(value, null);
        toList.push(previousAddress);
        return true;
      }
      const amount = validatePositiveFixedDecimal(value);
      if (amount) {
        if (previousAddress === null || previousAddress.amount !== null) {
          return false;
        }
        previousAddress.amount = amount;
      } else {
        return false;
      }
      return true;
    });

  if (isValid) {
    return toList;
  }

  return null;
}

const TOKENS = [
  {
    id: 0,
    name: "Celt",
    address: "0x5Ab622494AB7C5e81911558c9552DBd517F403fb",
    decimal: 18,
  },
  {
    id: 1,
    name: "Crystal",
    address: "0xBD87c06D865a3717A16041aCD8d33922e60E85CE",
    decimal: 18,
  },
  {
    id: 2,
    name: "CeltChest",
    address: "0x777c5a7152F999B4bBC26295C65a0ACb066f75e9",
    decimal: 0,
  },
];

const CONTRACT_ABIS = [
  {
    inputs: [
      { internalType: "address", name: "erc20Token", type: "address" },
      { internalType: "address[]", name: "targets", type: "address[]" },
      { internalType: "uint256", name: "amount", type: "uint256" },
    ],
    name: "airDropBatch",
    outputs: [],
    stateMutability: "nonpayable",
    type: "function",
  },
  {
    inputs: [
      { internalType: "address", name: "erc20Token", type: "address" },
      { internalType: "address[]", name: "targets", type: "address[]" },
      { internalType: "uint256[]", name: "amounts", type: "uint256[]" },
    ],
    name: "airDropIndividual",
    outputs: [],
    stateMutability: "nonpayable",
    type: "function",
  },
];

const ERC20_ABIS = [
  {
    constant: true,
    inputs: [{ name: "_owner", type: "address" }],
    name: "balanceOf",
    outputs: [{ name: "balance", type: "uint256" }],
    payable: false,
    stateMutability: "view",
    type: "function",
  },
  {
    constant: false,
    inputs: [
      { name: "_spender", type: "address" },
      { name: "_value", type: "uint256" },
    ],
    name: "approve",
    outputs: [{ name: "", type: "bool" }],
    payable: false,
    stateMutability: "nonpayable",
    type: "function",
  },
  {
    constant: true,
    inputs: [
      { name: "_owner", type: "address" },
      { name: "_spender", type: "address" },
    ],
    name: "allowance",
    outputs: [{ name: "", type: "uint256" }],
    payable: false,
    stateMutability: "view",
    type: "function",
  },
];

function getAllowance(
  web3: Web3,
  index: number,
  owner: string | null,
  sender: string,
): Promise<number> {
  if (!owner) {
    return Promise.resolve(0);
  }

  const { address } = TOKENS[index];

  const erc20Contract = new web3.eth.Contract(ERC20_ABIS as AbiItem[], address);

  // eslint-disable-next-line
  return erc20Contract.methods.allowance(owner, sender).call();
}

function approve(
  web3: Web3,
  index: number,
  from: string | null,
  sender: string,
  amount: bigint,
): Promise<void> {
  if (!from) {
    return Promise.resolve();
  }

  const { address } = TOKENS[index];

  const erc20Contract = new web3.eth.Contract(ERC20_ABIS as AbiItem[], address);

  // eslint-disable-next-line
  return erc20Contract.methods.approve(sender, amount).send({ from });
}

const SENDER = "0x074101c52ea69D66577057ECA07147bcF7B3f057";

function airdrop(
  web3: Web3,
  index: number,
  from: string | null,
  toList: To[] | null,
  defaultAmount: bigint | null,
  decimal: number,
  progressSetter: (_: number) => void,
  limit: number,
): Promise<void> {
  if (!from || !toList) {
    return Promise.resolve();
  }
  const unit = BigInt(10 ** decimal);

  const { address } = TOKENS[index];

  const contractInstance = new web3.eth.Contract(CONTRACT_ABIS as AbiItem[], SENDER);

  const defaultTo = Array.from(
    toList.filter((to) => to.amount === null),
    (to) => to.address,
  );
  const explicitedTo = Array.from(
    toList.filter((to) => to.amount !== null),
    (to) => to.address,
  );
  const explicitedToAmount = Array.from(
    toList.filter((to) => to.amount !== null),
    (to) => to.amount,
  );

  const airdrops = [];
  let progress = 0;
  if (defaultTo.length !== 0) {
    if (!defaultAmount) {
      return Promise.resolve();
    }
    for (let i = 0; i < defaultTo.length; i += limit) {
      const toSlice = defaultTo.slice(i, i + limit);
      airdrops.push(
        // eslint-disable-next-line
        contractInstance.methods
          .airDropBatch(address, toSlice, (defaultAmount * unit) / BigInt(10 ** 18))
          .send({ from })
          // eslint-disable-next-line
          .then(() => {
            progress += toSlice.length;
            progressSetter(progress);
          }),
      );
    }
  }

  if (explicitedTo.length !== 0) {
    for (let i = 0; i < explicitedTo.length; i += limit) {
      const toSlice = explicitedTo.slice(i, i + limit);
      const amountSlice = explicitedToAmount
        .slice(i, i + limit)
        .map((v: bigint | null) => {
          assert(v);
          return (v * unit) / BigInt(10 ** 18);
        });
      airdrops.push(
        // eslint-disable-next-line
        contractInstance.methods
          .airDropIndividual(address, toSlice, amountSlice)
          .send({ from })
          // eslint-disable-next-line
          .then(() => {
            progress += toSlice.length;
            progressSetter(progress);
          }),
      );
    }
  }

  return Promise.all(airdrops).then(EMPTY_FUNCTION);
}

export default function Home(): React.ReactElement {
  const ethereum = useContext<BaseProvider | null>(EthereumContext);
  assert(ethereum);
  // eslint-disable-next-line
  const web3 = new Web3(ethereum as any);
  const account = useContext<string | null>(AccountContext);

  const [randomAddressesQuantity, setRandomAddressesQuantity] = useState("");
  const [minAmountRaw, setMinAmountRaw] = useState("");
  const [minAmount, setMinAmount] = useState<bigint | null>(BigInt(0));
  const [maxAmountRaw, setMaxAmountRaw] = useState("");
  const [maxAmount, setMaxAmount] = useState<bigint | null>(BigInt(0));

  const [selectedToken, setSelectedToken] = useState(0);
  const [allowanceAmount, setAllowanceAmount] = useState<bigint>(BigInt(0));
  const [defaultAmountRaw, setDefaultAmountRaw] = useState("");
  const [defaultAmount, setDefaultAmount] = useState<bigint | null>(null);
  const [addressesPerTransaction, setAddressesPerTransaction] = useState("200");
  const [toRaw, setToRaw] = useState("");
  const [toList, setToList] = useState<To[] | null>(null);
  const [isApproving, setIsApproving] = useState(false);
  const [isGiving, setIsGiving] = useState(false);
  const [isUpdatingAllowance, setIsUpdatingAllowance] = useState(false);
  const [givedAddresses, setGivedAddresses] = useState(0);
  const [decimal, setDecimal] = useState("");

  const token = TOKENS[selectedToken];

  function updateAllowance() {
    setIsUpdatingAllowance(true);
    getAllowance(web3, selectedToken, account, SENDER)
      .then((amount: number) => {
        setAllowanceAmount(BigInt(amount));
        setIsUpdatingAllowance(false);
      }, EMPTY_FUNCTION);
  }
  useEffect(
    () => {
      updateAllowance();
      setDecimal(String(token.decimal));
    // eslint-disable-next-line
    }, [selectedToken, account],
  );

  function updateToRaw(raw: string) {
    setToRaw(raw);
    setToList(parseAddresses(raw));
  }

  const isDefaultAmountNeeded = toList !== null && toList.some((to) => to.amount === null);
  const isDefaultAmountInvalid = !defaultAmount && isDefaultAmountNeeded;

  return (
    <Container className="mt-4">
      <Row className="justify-content-center">
        <h1>Airdrop</h1>
      </Row>
      <Row className="justify-content-center mt-3">
        <InputGroup hasValidation>
          <Form.Control
            as="textarea"
            className="Home__Addresses"
            value={toRaw}
            onChange={(e) => updateToRaw(e.currentTarget.value)}
            isInvalid={toList === null}
          />
          <Form.Control.Feedback type="invalid">
            Please provide valid addresses with/without amount.
          </Form.Control.Feedback>
        </InputGroup>
      </Row>
      <Row className="justify-content-center mt-3">
        <Col xs="12" className="mt-3">
          <InputGroup>
            <InputGroup.Text>Random addresses quantity</InputGroup.Text>
            <Form.Control
              value={randomAddressesQuantity}
              placeholder="amount"
              onChange={
                (e) => updateIntegerByStr(e.currentTarget.value, setRandomAddressesQuantity)
              }
            />
          </InputGroup>
        </Col>
        <Col xs="12" md="6" className="mt-3">
          <InputGroup>
            <InputGroup.Text>Min amount</InputGroup.Text>
            <Form.Control
              value={minAmountRaw}
              placeholder="amount"
              onChange={(e) => updateAmountByStr(
                e.currentTarget.value,
                setMinAmountRaw,
                setMinAmount,
              )}
            />
          </InputGroup>
        </Col>
        <Col xs="12" md="6" className="mt-3">
          <InputGroup>
            <InputGroup.Text>Max amount</InputGroup.Text>
            <Form.Control
              value={maxAmountRaw}
              placeholder="amount"
              onChange={(e) => updateAmountByStr(
                e.currentTarget.value,
                setMaxAmountRaw,
                setMaxAmount,
              )}
            />
          </InputGroup>
        </Col>
        <Col xs="auto" className="mt-3">
          <Button
            onClick={() => {
              assert(maxAmount && minAmount);
              const result = [];
              for (let i = 0; i < Number(randomAddressesQuantity); i += 1) {
                const { address } = web3.eth.accounts.create();
                const amount = BigInt(
                  Math.floor(Number(maxAmount - minAmount) * Math.random()),
                ) + minAmount;
                result.push(`${address} ${fixedDecimalToStr(amount, Number(decimal))}`);
              }
              updateToRaw(result.join("\n"));
            }}
            disabled={!minAmount || !maxAmount || randomAddressesQuantity === ""}
          >
            Generate
          </Button>
        </Col>
      </Row>
      <Row className="justify-content-center mt-3">
        <Col xs="auto">
          <ButtonGroup>
            {TOKENS.map(({ id, name }) => (
              <ToggleButton
                key={id}
                id={`token-${id}`}
                name="tokens-radio"
                type="radio"
                variant="outline-primary"
                value={id}
                checked={selectedToken === id}
                onChange={(e) => setSelectedToken(Number(e.currentTarget.value))}
              >
                {name}
              </ToggleButton>
            ))}
          </ButtonGroup>
        </Col>
        <Col xs="auto" className="d-flex align-items-center">
          <Spinner
            as="span"
            animation="border"
            size="sm"
            role="status"
            aria-hidden="true"
            className={`me-1 ${isUpdatingAllowance ? "" : "d-none"}`}
          />
          <p className="m-0">
            {token.address}
            &nbsp;default decimal:&nbsp;
            {token.decimal}
          </p>
        </Col>
        <div className="w-100" />
        <Col xs="12" md="6" className="mt-3">
          <InputGroup hasValidation>
            <InputGroup.Text>Default amount</InputGroup.Text>
            <Form.Control
              value={defaultAmountRaw}
              placeholder="amount"
              onChange={(e) => updateAmountByStr(
                e.currentTarget.value,
                setDefaultAmountRaw,
                setDefaultAmount,
              )}
              isInvalid={isDefaultAmountInvalid}
            />
            <Form.Control.Feedback type="invalid">
              Please provide amount for default.
            </Form.Control.Feedback>
          </InputGroup>
        </Col>
        <Col xs="12" md="6" className="mt-3">
          <InputGroup>
            <InputGroup.Text>Addresses per transaction</InputGroup.Text>
            <Form.Control
              value={addressesPerTransaction}
              placeholder="addresses number"
              onChange={
                (e) => updateIntegerByStr(e.currentTarget.value, setAddressesPerTransaction)
              }
            />
          </InputGroup>
        </Col>
        <Col xs="12" className="mt-3">
          <InputGroup>
            <InputGroup.Text>Decimal</InputGroup.Text>
            <Form.Control
              value={decimal}
              placeholder="decimal"
              onChange={(e) => {
                const { value } = e.currentTarget;
                updateIntegerByStr(value, setDecimal);
                if (value === "0") {
                  setDecimal(value);
                }
              }}
            />
          </InputGroup>
        </Col>
        <Col xs="auto" className="mt-3">
          <Button
            onClick={async () => {
              setIsApproving(true);
              try {
                await approve(
                  web3,
                  selectedToken,
                  account,
                  SENDER,
                  // 2 ** 256 - 1
                  BigInt("115792089237316195423570985008687907853269984665640564039457584007913129639935"),
                );
              } finally {
                setIsApproving(false);
              }
              updateAllowance();
            }}
            disabled={isApproving}
            className={allowanceAmount !== BigInt(0) ? "d-none" : ""}
          >
            <Spinner
              as="span"
              animation="border"
              size="sm"
              role="status"
              aria-hidden="true"
              className={`me-1 ${isApproving ? "" : "d-none"}`}
            />
            Approve
          </Button>
        </Col>
        <Col xs="auto" className="mt-3">
          <Button
            variant="success"
            onClick={async () => {
              setGivedAddresses(0);
              setIsGiving(true);
              try {
                await airdrop(
                  web3,
                  selectedToken,
                  account,
                  toList,
                  defaultAmount,
                  Number(decimal),
                  setGivedAddresses,
                  Number(addressesPerTransaction),
                );
              } finally {
                setIsGiving(false);
              }
              updateAllowance();
            }}
            disabled={
              toList === null
              || isDefaultAmountInvalid
              || isGiving
              || !toList.every((to) => web3.utils.isAddress(to.address))
              || allowanceAmount === BigInt(0)
              || decimal === ""
              || addressesPerTransaction === ""
            }
          >
            <Spinner
              as="span"
              animation="border"
              size="sm"
              role="status"
              aria-hidden="true"
              className={`me-1 ${isGiving ? "" : "d-none"}`}
            />
            Airdrop
            <span className={isGiving ? "" : "d-none"}>
              &nbsp;
              {givedAddresses}
              /
              {toList?.length}
            </span>
          </Button>
        </Col>
      </Row>
      <Row className="mt-3">
        <Col>
          <Table>
            <thead>
              <tr>
                <th>Address</th>
                <th>Amount</th>
              </tr>
            </thead>
            <tbody>
              {
                toList
                  ? toList.map((to) => (
                    <tr>
                      <td>
                        {to.address}
                        <span className={`ms-4 text-danger ${web3.utils.isAddress(to.address) ? "d-none" : ""}`}>
                          Invalid address
                        </span>
                      </td>
                      <td className={to.amount ? "" : "text-secondary"}>
                        {
                          (() => {
                            if (to.amount) {
                              return fixedDecimalToStr(to.amount, Number(decimal));
                            }
                            if (defaultAmount) {
                              return fixedDecimalToStr(defaultAmount, Number(decimal));
                            }
                            return "";
                          })()
                        }
                      </td>
                    </tr>
                  ))
                  : []
              }
            </tbody>
          </Table>
        </Col>
      </Row>
    </Container>
  );
}
