function parseAddresses(raw: string): string[] | null {
  const toList: string[] = [];

  const isValid = raw
    .replaceAll(",", " ")
    .replaceAll("\n", " ")
    .replaceAll('"', "")
    .replaceAll("'", "")
    .replaceAll("`", "")
    .replaceAll(/  */g, " ")
    .trim()
    .split(" ")
    .every((value) => {
      if (!value.startsWith("0x")) {
        return false;
      }
      toList.push(value);
      return true;
    });

  if (isValid) {
    return toList;
  }

  return null;
}

