const SENDER = "0x3a9AF5ea13625984EFFBf0cCc1c72580e3e3b7F1";

function airdropMaintainer(
  web3: Web3,
  from: string | null,
  toList: string[] | null,
  quality: number,
  intention: number,
  level: number,
  specificValues: Array<bigint | null>,
  skin: number,
  progressSetter: (_: number) => void,
  limit: number,
): Promise<void> {
  if (!from || !toList || specificValues.some((e) => !e)) {
    return Promise.resolve();
  }

  const contractInstance = new web3.eth.Contract(CONTRACT_ABIS as AbiItem[], SENDER);

  const airdrops = [];
  let progress = 0;
  for (let i = 0; i < toList.length; i += limit) {
    const toSlice = toList.slice(i, i + limit);
    airdrops.push(
      // eslint-disable-next-line
      contractInstance.methods
        .forgeMaintainer(toSlice, quality, intention, level, specificValues, skin)
        .send({ from })
        // eslint-disable-next-line
        .then(() => {
          progress += toSlice.length;
          progressSetter(progress);
        }),
    );
  }

  return Promise.all(airdrops).then(EMPTY_FUNCTION);
}

function airdropCaptainMaintainer(
  web3: Web3,
  from: string | null,
  toList: string[] | null,
  intention: number,
  level: number,
  specificValues: Array<bigint | null>,
  progressSetter: (_: number) => void,
  limit: number,
): Promise<void> {
  if (!from || !toList || specificValues.some((e) => !e)) {
    return Promise.resolve();
  }

  const contractInstance = new web3.eth.Contract(CONTRACT_ABIS as AbiItem[], SENDER);

  const airdrops = [];
  let progress = 0;
  for (let i = 0; i < toList.length; i += limit) {
    const toSlice = toList.slice(i, i + limit);
    airdrops.push(
      // eslint-disable-next-line
      contractInstance.methods
        .forgeCaptainMaintainer(toSlice, intention, level, specificValues)
        .send({ from })
        // eslint-disable-next-line
        .then(() => {
          progress += toSlice.length;
          progressSetter(progress);
        }),
    );
  }

  return Promise.all(airdrops).then(EMPTY_FUNCTION);
}

export default function NFT(): React.ReactElement {
  const ethereum = useContext<BaseProvider | null>(EthereumContext);
  assert(ethereum);
  // eslint-disable-next-line
  const web3 = new Web3(ethereum as any);
  const account = useContext<string | null>(AccountContext);

  const [selectedNFT, setSelectedNFT] = useState(0);
  const [selectedQuality, setSelectedQuality] = useState(40000);
  const [selectedIntention, setSelectedIntention] = useState(1);
  const [specificValues, setSpecificValues] = useState<Array<bigint | null>>([
    null, null, null, null, null, null,
  ]);
  const [selectedSkin, setSelectedSkin] = useState(0);
  const [skins, setSkins] = useState(SKINS);
  const [level, setLevel] = useState<bigint | null>(null);
  const [addressesPerTransaction, setAddressesPerTransaction] = useState<bigint | null>(BigInt(50));
  const [toList, setToList] = useState<string[] | null>(null);
  const [isGiving, setIsGiving] = useState(false);
  const [givedAddresses, setGivedAddresses] = useState(0);

  const specificValuesInvalid = (
    selectedIntention === 5
    && (
      (selectedNFT === 0 && specificValues.slice(0, 6).some((e) => e === null))
      || (
        selectedNFT === 1
        && specificValues.slice(0, 5).some((e) => e === null)
      )
    )
  );

  useEffect(() => {
    const contractInstance = new web3.eth.Contract(CONTRACT_ABIS as AbiItem[], SENDER);

    // eslint-disable-next-line
    contractInstance.methods
      .skinRegistry()
      .call()
      // eslint-disable-next-line
      .then(
        (skinResult: Array<{ description: string, skinStats: string }>) => setSkins(
          skinResult.map(({ description, skinStats }, index) => ({
            id: index,
            name: hexToUtf8(description),
            value: Number(skinStats),
          })),
        ),
      );
  // eslint-disable-next-line
  }, [ethereum]);

  return (
    <Container className="mt-4">
      <Row className="justify-content-center">
        <h1>NFT airdrop</h1>
      </Row>
      <Row className="justify-content-center mt-3">
        <InputGroup hasValidation>
          <Form.Control
            as="textarea"
            className="NFT__Addresses"
            onChange={(e) => setToList(parseAddresses(e.currentTarget.value))}
            isInvalid={toList === null}
          />
          <Form.Control.Feedback type="invalid">
            Please provide valid addresses.
          </Form.Control.Feedback>
        </InputGroup>
      </Row>
      <Row className="justify-content-center mt-3">
        <Col xs="auto">
          <ToggleButtons selects={NFTS} groupName="nft" onChange={setSelectedNFT} />
        </Col>
        <Col xs="auto" className={selectedNFT === 0 ? "" : "d-none"}>
          <ToggleButtons selects={QUALITIES} groupName="quality" onChange={setSelectedQuality} />
        </Col>
        <Col xs="auto">
          <ToggleButtons selects={INTENTIONS} groupName="intention" onChange={setSelectedIntention} />
        </Col>
        <Col xs="12" className={selectedNFT === 0 ? "mt-3" : "d-none"}>
          <ToggleButtons selects={skins} groupName="skin" onChange={setSelectedSkin} />
        </Col>
        <div className="w-100" />
        {
          selectedIntention === 5
          && selectedNFT === 0
          && WARSHIP_SPECIFIC_VALUES.map(({
            id,
            name,
            range,
            decimal,
          }) => {
            const [min, max] = selectedQuality === 40000 ? range[0] : range[1];
            return (
              <Col xs="12" md="6" className="mt-3" key={id}>
                <RangeInput
                  text={name}
                  placeholder={`${BigInt(min)} ~ ${BigInt(max)}`}
                  defaultValue={String(min)}
                  min={BigInt(min)}
                  max={BigInt(max)}
                  decimal={decimal}
                  onChange={(v) => setSpecificValues((o) => {
                    const result = [...o];
                    result[id] = v;
                    return result;
                  })}
                />
              </Col>
            );
          })
        }
        {
          selectedIntention === 5
          && selectedNFT === 1
          && CAPTAIN_SPECIFIC_VALUES.map(({
            id,
            name,
            range,
            decimal,
          }) => {
            const [min, max] = range;
            return (
              <Col xs="12" md="6" className="mt-3" key={id}>
                <RangeInput
                  text={name}
                  placeholder={`${BigInt(min)} ~ ${BigInt(max)}`}
                  defaultValue={String(min)}
                  min={BigInt(min)}
                  max={BigInt(max)}
                  decimal={decimal}
                  onChange={(v) => setSpecificValues((o) => {
                    const result = [...o];
                    result[id] = v;
                    return result;
                  })}
                />
              </Col>
            );
          })
        }
        <div className="w-100" />
        <Col xs="12" md="6" className="mt-3">
          <RangeInput
            text="Level"
            placeholder="level number"
            defaultValue=""
            min={BigInt(1)}
            max={BigInt(31)}
            onChange={setLevel}
          />
        </Col>
        <Col xs="12" md="6" className="mt-3">
          <RangeInput
            text="Addresses per transaction"
            placeholder="addresses number"
            defaultValue="50"
            min={BigInt(1)}
            max={null}
            onChange={setAddressesPerTransaction}
          />
        </Col>
        <Col xs="auto" className="mt-3">
          <Button
            variant="success"
            onClick={async () => {
              setGivedAddresses(0);
              setIsGiving(true);
              try {
                if (selectedNFT === 0) {
                  await airdropMaintainer(
                    web3,
                    account,
                    toList,
                    selectedQuality,
                    selectedIntention,
                    Number(level),
                    selectedIntention === 5 ? specificValues : [],
                    selectedSkin,
                    setGivedAddresses,
                    Number(addressesPerTransaction),
                  );
                } else {
                  await airdropCaptainMaintainer(
                    web3,
                    account,
                    toList,
                    selectedIntention,
                    Number(level),
                    selectedIntention === 5 ? specificValues.slice(0, 5) : [],
                    setGivedAddresses,
                    Number(addressesPerTransaction),
                  );
                }
              } finally {
                setIsGiving(false);
              }
            }}
            disabled={
              toList === null
              || isGiving
              || !toList.every((to) => web3.utils.isAddress(to))
              || !level
              || specificValuesInvalid
            }
          >
            <Spinner
              as="span"
              animation="border"
              size="sm"
              role="status"
              aria-hidden="true"
              className={`me-1 ${isGiving ? "" : "d-none"}`}
            />
            Airdrop
            <span className={isGiving ? "" : "d-none"}>
              &nbsp;
              {givedAddresses}
              /
              {toList?.length}
            </span>
          </Button>
        </Col>
      </Row>
      <Row className="mt-3">
        <Col>
          <Table>
            <thead>
              <tr>
                <th>Address</th>
              </tr>
            </thead>
            <tbody>
              {
                toList
                  ? toList.map((to) => (
                    <tr>
                      <td>
                        {to}

                        <span className={`ms-4 text-danger ${web3.utils.isAddress(to) ? "d-none" : ""}`}>
                          Invalid address
                        </span>
                      </td>
                    </tr>
                  ))
                  : []
              }
            </tbody>
          </Table>
        </Col>
      </Row>
    </Container>
  );
}
